
import { defineComponent, reactive, ref } from "vue"
import { listVideo } from "./video/list"
export default defineComponent({
  setup() {
    const current = ref(0);
    const state = reactive({
      listVideo
    })
    const onChange = (index: any) => {
      current.value = index;
    };
    return {
      state, 
      current,
      onChange,
    };
  },
})
